
.clockContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 49px;

}
.stopWatchTitle{
  /*  font-family: "Inter", sans-serif;*/
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings:
            "slnt" 0;
    margin-bottom: 59px;
    font-size: 130px;
    color: #969595;
}
.timeBox {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 293px;
    width:334px;
    background-color: #525252;
    color: #fff;
    font-size: 210px;
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: normal;
    font-variation-settings:"slnt" 0;
}
.backArrow{
    margin-right:1100px ;
    color: #9b9b9b;
}

.body{
    background-color: #1E1E1E;
    text-decoration: none;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 295px;
    margin-top: 75px;
}
.startButton {
    height: 87px;
    width: 85px;
    background-color: rgba(49, 134, 35, 0.25); /* Цвет с 25% opacity */
    color: white;
    border-radius: 50%;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); /* Добавляет тень */
    transition: opacity 0.3s ease, box-shadow 0.3s ease; /* Плавный переход при наведении */
}

.startButton:hover {
    opacity: 1; /* Возвращает яркость при наведении */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5); /* Усиливает тень при наведении */
}
.stopButton {
    height: 87px;
    width: 85px;
    background-color: rgba(194, 84, 60, 0.25); /* Цвет с 25% opacity */
